<template>
    <div class="open-ended-question d-flex pt-3" v-loading="loading">
        <el-input placeholder="Введите ваш ответ"
                  class="mr-2"
                  v-model="selected"
                  v-if="survey"
        ></el-input>
        <el-button type="primary" size="small" @click="handleChange(selected)">Отправить</el-button>
    </div>
</template>

<script>
    import {SET_SURVEY_ANSWER, SURVEYS} from "../../../store/types/surveys";
    export default {
        name: 'open-ended',
        data() {
            return {
                selected: '',
                loading: false
            }
        },
        props: {
            survey: {},
            setNumber: Number
        },
        methods: {
          handleChange(value) {
            console.log(value)
            if (!value || value === '') return
            this.loading = true
            const setNumber = this.setNumber
            const questionNumber = this.survey.questionNumber
            this.$store.dispatch(SURVEYS + SET_SURVEY_ANSWER, {setNumber, questionNumber, answers: [value]})
              .then(() => {
                this.loading = false
              })
              .catch(() => {
                this.loading = false
              })
          },
          handleSelectedAnswer(survey) {
            if (survey.selectedAnswer && survey.selectedAnswer[0]) {
              this.selected = survey.selectedAnswer[0]
            }
          }
        },
        watch: {
            survey(to) {
              this.handleSelectedAnswer(to)
            }
        },
        mounted() {
            if (this.survey) {
              this.handleSelectedAnswer(this.survey)
            }
        }
    }
</script>