<template>
    <div class="p-0">
        <user-task-control v-if="isLoaded && isLogged === USER"></user-task-control>
        <company-task-control v-if="isLoaded && isLogged === COMPANY"></company-task-control>
    </div>
</template>

<script>
    import {COMPANY, GET_STATUS, USER} from "../store/mutation-types";
    import {COMPANY_PROFILE} from "../store/types/companyProfile";
    import {USER_PROFILE} from "../store/types/userProfile";
    import ProfilePlaceholder from "../components/ProfilePlaceholder";
    import UserTaskControl from "../components/UserProfile/UserTaskControl";
    import CompanyTaskControl from "../components/CompanyProfile/CompanyTaskControl";

    export default {
        name: 'task-control',
        components: {
            CompanyTaskControl,
            UserTaskControl,
            ProfilePlaceholder,
        },
        computed: {
            isLogged() {
                return this.$store.getters['auth/isLogged']
            },
            profileStore() {
                if (this.isLogged === COMPANY) return COMPANY_PROFILE
                if (this.isLogged === USER) return USER_PROFILE
                return false
            },
            isLoaded() {
                return this.profileStore ? this.$store.getters[this.profileStore + GET_STATUS] === 'success' : false
            }
        },
        data() {
            return {
                USER,
                COMPANY
            }
        },
    }
</script>