<template>
    <div class="filter-vacancy">
        <div class="d-flex">
            <el-input placeholder="Поиск" v-model="search.value" @keyup.enter.native="clientFilter" clearable @clear="clientFilter">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
                <template slot="append">
                    <el-button type="primary" @click="clientFilter">Поиск</el-button>
                </template>
            </el-input>
        </div>
    </div>
</template>

<script>
  import {SET_FILTERS} from "../../store/mutation-types";
  import {COMPANIES} from "../../store/types/companies";

  export default {
    name: 'filter-company',
    data() {
      return {
        search: {
          value: ''
        },
      }
    },
    methods: {
      clientFilter() {
        this.$store.commit(COMPANIES + SET_FILTERS, this.search.value)
      }
    }
  }
</script>
