                                        </li>
                                    </ul>
                                </div>
                            </template>
                            <div style="opacity: .5">
                                <p class="mb-0">Заработная плата: {{vacancy.cost}} <span style="font-size: .75em">₸</span></p>
                                <!--<p class="mb-0" v-if="vacancy.deadline">Срок сдачи: {{vacancy.deadline | moment('Do MMMM, YYYY')}}</p>-->
                            </div>
                        </div>
                        <el-tooltip class="item" effect="dark" content="Переместить в архив" placement="left">
                            <el-button type="danger" icon="el-icon-delete" circle size="small" @click="removeVacancy(vacancy)"></el-button>
                        </el-tooltip>
                    </div>
                </div>
            </div>
            <el-dialog title="Новая вакансия" width="500px" :visible.sync="dialogFormVisible" v-if="dialogFormVisible">
                <new-vacancy @success="dialogFormVisible = false; loadVacancies()" @cancel="dialogFormVisible = false"></new-vacancy>
            </el-dialog>
