<script>
    import NewVacancy from '../Forms/NewVacancy.vue'
    import {COMPANY_PROFILE, GET_PROFILE} from '../../store/types/companyProfile'
    import {GET_OWN_VACANCIES, VACANCIES} from "../../store/types/vacancies"
    import { mask } from 'vue-the-mask'
    import {AUTH, GET_TOKEN} from "../../store/types/auth";
    import api, {baseURL} from "../../store/api/main";
    import {GET_STATUS, REMOVE_VACANCY} from '../../store/mutation-types'

    export default {
        name: 'company-profile',
        data() {
            return {
                baseURL: baseURL,
                dialogFormVisible: false,
                loading: false
            }
        },
        components: {
            NewVacancy
        },
        directives: {mask},
        computed: {
            vacancies() {
                return this.$store.getters[VACANCIES + GET_OWN_VACANCIES]// Загрузка списка вакансии из store
            },
            company() {
                return this.$store.getters[COMPANY_PROFILE + GET_PROFILE]
            },
            token() {
                return this.$store.getters[AUTH + GET_TOKEN]
            },
            companyStatus() {
                return this.$store.getters[COMPANY_PROFILE + GET_STATUS]
            },
            edit() {
                return !!this.$route.query.edit;
            },

        },
        methods: {
            removeVacancy(vacancy) {
              this.$store.commit(VACANCIES + REMOVE_VACANCY, vacancy.id)
            },
            loadVacancies() {
                this.$store.dispatch(VACANCIES + GET_OWN_VACANCIES)
            },
            handleAvatarSuccess(res, file) {
                this.company.image = URL.createObjectURL(file.raw);
                this.loading = false
            },
            beforeAvatarUpload(file) {
                const isPNG = file.type === 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isPNG) {
                    this.$message.error('Аватар должен быть в формате PNG!')
                }
                if (!isLt2M) {
                    this.$message.error('Размер файла не должен превышать 2MB!')
                }
                this.loading = isPNG && isLt2M;
                return this.loading
            },
            async saveData() {
                this.loading = true;
                await api.put('company/private/name',{name: this.company.name});
                await api.put('company/private/phone',{phone: this.company.phone});
                this.loading = false;
                this.$router.push('/profile');
            }
        },
        mounted() {
          this.loadVacancies()
        }
    }
</script>

