<template>
    <div class="user-actions">
        <el-button-group class="w-100" size="small">
            <el-tooltip :enterable="false" :disabled="modalVisible" class="item" effect="dark" content="Информация" placement="left">
                <el-button class="w-50" icon="far fa-address-card" size="mini" @click="dialogVisible = true"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" :disabled="modalVisible" class="item" effect="dark" content="Написать" placement="right">
                <el-button type="warning" class="w-50" icon="el-icon-message" size="mini" @click="chatVisible = true"></el-button>
            </el-tooltip>
        </el-button-group>
        <el-dialog
                title="Информация"
                :visible.sync="dialogVisible"
                width="30%">
            <template v-if="person.description">
                <p v-html="person.description" class="mb-3"></p>
            </template>
            <template v-if="person.credentials.phone || person.credentials.email">
                <p v-if="person.credentials.phone">Телефон: <a target="_blank" :href="'tel:'  +  person.credentials.phone">{{person.credentials.phone}}</a></p>
                <p v-if="person.credentials.email">Почта: <a target="_blank" :href="'mailto:'  +  person.credentials.email">{{person.credentials.email}}</a></p>
            </template>
            <template v-else>
                <h4>Не имеет контактов</h4>
            </template>
        </el-dialog>
        <chat-send-message :receiverTitle="(person.firstName ? person.firstName : person.credentials.email)"
                           :chatVisible.sync="chatVisible"
                           :receiverId="person.id">
        </chat-send-message>
    </div>
</template>

<script>
    import ChatSendMessage from "../Chat/ChatSendMessage";
    export default {
        components: {ChatSendMessage},
        name: 'user-actions',
        data() {
            return {
                dialogVisible: false,
                chatVisible: false,
                messageText: ''
            }
        },
        computed: {
          modalVisible() {
            return this.dialogVisible || this.chatVisible
          },
        },
        props: {
            person: {
                type: Object,
                required: true
            }
        }
    }
</script>