<template>
    <div class="user-search">
        <div class="px-0 py-5 px-md-5">
            <h2 class="mb-5">Поиск компании</h2>
            <filter-company></filter-company>
            <div class="row my-5 py-5" v-loading="isLoading">
                <company-item class="col-12 mb-5" v-for="(company, index) in companies" :key="index + '-company'"  :company="company"/>
            </div>
            <el-pagination
              v-if="totalCount > pageSize"
              class="text-center"
              background
              layout="prev, pager, next"
              @current-change="pageChange"
              :current-page="currentPage"
              :page-size="pageSize"
              :total="totalCount"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
  import {COMPANY, USER} from "../../store/mutation-types";
  import {COMPANIES, GET_ALL_COMPANIES, GET_COMPANIES_FLEX, GET_TOTAL_COUNT} from '../../store/types/companies'
  import {GET_PROFILE, USER_PROFILE} from "../../store/types/userProfile";
  import FilterCompany from "./FilterCompany";
  import CompanyItem from './CompanyItem'

    export default {
      name: 'user-search',
      components: {CompanyItem, FilterCompany},
      data() {
        return {
          USER: USER,
          COMPANY: COMPANY,
          pageSize: 10,
          currentPage: 1
        }
      },
      computed: {
        companies(){
          return this.$store.getters[COMPANIES + GET_COMPANIES_FLEX]
        },
        isLogged() {
          return this.$store.getters['auth/isLogged'] // Проверка на логин через store
        },
        isLoading(){
          return this.$store.state.companies.status === 'loading'
        },
        totalCount() {
          return this.$store.getters[COMPANIES + GET_TOTAL_COUNT]
        }
      },
      mounted() {
        if (this.$store.state.companyProfile.status === 'clean') {
          this.$store.dispatch(USER_PROFILE + GET_PROFILE)
        }
        this.loadCompanies(this.currentPage-1, this.pageSize)
      },
      methods: {
        pageChange(page) {
          this.loadCompanies(page-1, this.pageSize);
        },
        loadCompanies(page=0, count=10) {
          this.$store.dispatch(COMPANIES + GET_ALL_COMPANIES, {page, count});
        }
      },
      beforeMount() {
        // Временный обход security роутинга, для не зарегестрированных пользователей
        const isLogged = this.$store.getters['auth/isLogged'];
        if (isLogged !== USER) {
          this.$router.push('/')
        }
      }
    }
</script>
