import api, {baseURL} from '../api/main'
import {SET_FILTERS} from "../mutation-types";
import {
  COMPANY_CLEAN,
  COMPANY_ERROR,
  COMPANY_REQUEST, COMPANY_SUCCESS,
  GET_ALL_COMPANIES,
  GET_COMPANIES_FLEX,
  GET_COMPANY,
  GET_FILTERED_COMPANIES, GET_TOTAL_COUNT, MERGE_COMPANIES, MERGE_COMPANY,
} from '../types/companies'
import {SET_TOTAL_COUNT} from '../types/vacancies'

const state = {
    all: {},
    list: [],
    status: 'clear',
    filter: '',
  totalCount: 0,
}


const getters = {
    [GET_ALL_COMPANIES]: (state) => {
        return Object.values(state.all).sort((a, b) => a.order - b.order).filter(s => {
            return s.credentials
        })
    },
    [GET_TOTAL_COUNT]: (state) => state.totalCount,
    [GET_COMPANY]: state => id => {
        return state.all[id]
    },
    [GET_FILTERED_COMPANIES]: (state, getters) => {
        return getters[GET_ALL_COMPANIES].filter(s => {
            const searchStr = s.firstName + ' '
              + s.lastName + ' '
              + s.description + ' '
              + s.credentials.email
            return searchStr.toLowerCase().indexOf(state.filter.toLowerCase()) !== -1
            
        })
    },
    [GET_COMPANIES_FLEX]: (state, getters) => {
        if (state.filter !== '') return getters[GET_FILTERED_COMPANIES]
        return getters[GET_ALL_COMPANIES]
    }
}

const mutations = {
    [COMPANY_REQUEST] (state) {
        state.status = 'loading'
    },
    [COMPANY_SUCCESS] (state) {
        state.status = 'success'
    },
    [COMPANY_ERROR] (state) {
        state.status = 'error'
    },
    [COMPANY_CLEAN] (state) {
        state.status = 'clean'
        state.all = {}
        state.list = []
    },
    [MERGE_COMPANIES](state, payload) {
        const all = {}
        payload.forEach(i => {
            i.id = i._id || i.id
            delete i._id
            all[i.id] = i
        })
        state.all = JSON.parse(JSON.stringify(all))
        state.list = Object.values(state.all).sort((a, b) => a.order - b.order)
    },
    [MERGE_COMPANY](state, payload) {
        payload.id = payload._id || payload.id
        delete payload._id
        payload.order = state.all[payload.id].order || state.all.length
        state.all[payload.id] = payload
        state.all = JSON.parse(JSON.stringify(state.all))
        state.list = Object.values(state.all).sort((a, b) => a.order - b.order)
    },
    [SET_FILTERS](state, payload) {
        state.filter = payload
    },
  [SET_TOTAL_COUNT](state, totalCount) {
        state.totalCount = totalCount;
  }
}
const actions = {
    [GET_ALL_COMPANIES]({commit}, {page=0, count=10}) {
        console.log(page, count);
        commit(COMPANY_REQUEST)
        return api.post(`company/${page}/${count}`)
          .then(res => {
              console.log(res);
              const data = res.data.result.map((i, index) => {
                  i.order = index;
                  i.image = baseURL + '/company/image-avatar/' + i._id + '.png';
                  return i });
            const totalCount = res.data.totalCount;
            commit(SET_TOTAL_COUNT, totalCount);
            commit(MERGE_COMPANIES, data)
              commit(COMPANY_SUCCESS)
              return data
          })
          .catch(err => {
              console.log(err)
              commit(COMPANY_ERROR)
              return err
          })
    },
    [GET_COMPANY]({commit}, payload) {
        commit(COMPANY_REQUEST)
        return api.post('company/' + payload)
          .then(res => {
              res.data.image = baseURL + '/company/image-avatar/' + res.data._id + '.png'
              commit(MERGE_COMPANY, res.data)
              commit(COMPANY_SUCCESS)
          })
          .catch(err => {
              console.log(err)
              commit(COMPANY_ERROR)
          })
    }
}
export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}
