<template>
    <el-radio-group :value="loginType" size="mini" class="modal-radio" @change="change">
        <el-radio-button label="student" name="student">сотрудник</el-radio-button>
        <el-radio-button label="company" name="company">работодатель</el-radio-button>
    </el-radio-group>
</template>

<script>
    export default {
      name: 'modal-radio',
      props: {
        loginType: String,
        actionType: String
      },
      data() {
        return {
          model: this.loginType
        }
      },
      methods: {
        change() {
          if(this.actionType === 'login' && this.loginType === 'company') this.$router.push('/login');
          else if(this.actionType === 'login' && this.loginType === 'student') this.$router.push('/login-company');
          else if(this.actionType === 'register'  && this.loginType === 'company') this.$router.push('/register');
          else if(this.actionType === 'register'  && this.loginType === 'student') this.$router.push('/register-company')
        }
      }
    }
</script>

<style scoped>
    .el-radio-group {
        margin: 0 auto;
        min-width: 100%;
        max-width: 320px;
    }
    .el-radio-group >>> .el-radio-button {
        width: 50%;
    }
    .el-radio-group >>> .el-radio-button__inner {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
    }
</style>