<template>
    <div class="vacancies container my-5 py-5">
        <h2 class="text-center mb-4">Все вакансии</h2>
        <filter-vacancy @filter="onFilter($event)"></filter-vacancy>
        <div class="row my-5" v-if="vacancies.length > 0" v-loading="isLoading !== 'success'">
            <vacancy-item class="col-12 mb-5 py-5" v-for="vacancy in vacancies" :key="vacancy.id" :vacancyId="vacancy.id" type="information"></vacancy-item>
        </div>
        <div class="py-5 text-center" v-if="vacancies.length < 1" v-loading="isLoading !== 'success'">
            <h3>Вакансии не найдены</h3>
        </div>
        <el-pagination
          v-if="totalCount > pageSize"
          class="text-center"
          background
          layout="prev, pager, next"
          @current-change="pageChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="totalCount"
        >
        </el-pagination>
    </div>
</template>

<script>
    import VacancyItem from '../components/UserProfile/VacancyItem.vue'
    import FilterVacancy from '../components/UserProfile/FilterVacancy.vue'
    import {
        GET_ALL_STUDENT_VACANCIES,
        GET_ALL_VACANCIES,
        GET_FILTERED_VACANCIES,
        GET_LOAD_VACANCIES,
        GET_LOADING_STATUS,
        VACANCIES
    } from "../store/types/vacancies";
    import {AUTH, IS_LOGGED} from "../store/types/auth";
    import {USER} from "../store/mutation-types";
    import {GET_TOTAL_COUNT} from '../store/types/companies'

    export default {
        components: { VacancyItem, FilterVacancy },
        name: 'vacancies',
        data() {
            return {
              pageSize: 2,
              currentPage: 1
            }
        },
        computed: {
            isLogged() {
                return this.$store.getters[AUTH + IS_LOGGED]
            },
            vacancies() {
                return this.$store.getters[VACANCIES + GET_LOAD_VACANCIES]
            },
            isLoading() {
                return this.$store.getters[VACANCIES + GET_LOADING_STATUS]
            },
          totalCount() {
            return this.$store.getters[VACANCIES + GET_TOTAL_COUNT]
          }
        },
        methods: {
            onFilter(filters) {
                this.$store.dispatch(VACANCIES + GET_FILTERED_VACANCIES, filters)
            },
          loadVacancies(page=0, count=10) {
            this.$store.dispatch(VACANCIES + GET_ALL_VACANCIES, {page, count});
            if (this.isLogged === USER) {
              this.$store.dispatch(VACANCIES + GET_ALL_STUDENT_VACANCIES, {page, count})
            } else {
              this.$store.dispatch(VACANCIES + GET_ALL_VACANCIES, {page, count})
            }
          },
          pageChange(page) {
              this.loadVacancies(page-1, this.pageSize)
          }
        },
        mounted() {
            this.loadVacancies(this.currentPage - 1, this.pageSize)
        },
    }
</script>

<style scoped="">
    .vacancies {
        min-height: 100vh;
        min-height: calc(100vh - 335px);
    }
</style>
