<template>
  <div class="company-view px-0 py-5 px-md-5">
    <div v-loading="!company"></div>
    <template v-if="company">
      <div class="d-flex profile-info mb-5">
        <div class="mr-3 mr-md-5">
          <div class="image-container">
            <div class="profile-image" :style="{backgroundImage: `url(${company.image})`}"></div>
          </div>
        </div>
        <div class="mr-sm-5">
          <div class="d-flex align-items-center mb-3">
            <h3 class="mr-3">{{company.name}}</h3>
          </div>
          <div class="mb-3" style="white-space: nowrap">
            <p v-if="company.email">
              <a :href="`mailto: ${company.email}`"><i class="far fa-envelope mr-2"></i> {{company.email}}</a>
            </p>
            <p v-if="company.phone">
              <a :href="`tel: ${company.phone}`"><i class="fa fa-mobile-alt mr-2"></i> {{company.phone}}</a>
            </p>
          </div>
        </div>
      </div>
      <div class="py-5" v-if="company.description">
        <h3>О компании</h3>
        <p v-html="company.description"></p>
      </div>
      <template v-if="!vacancies || !vacancies.length">
        <h3 style="opacity: .7;">У компании нет вакансии</h3>
      </template>
      <template v-if="vacancies && vacancies.length">
        <div class="py-5 my-5">
          <div class="d-flex mb-5 align-items-center">
            <h3 class="mr-5">Вакансии</h3>
          </div>
          <vacancy-item class="mb-5 pb-5" v-for="vacancy in vacancies" :key="vacancy.id" :vacancyId="vacancy.id" no-image></vacancy-item>
        </div>
      </template>
    </template>
  </div>
</template>

<script>
  import companyService from "../services/company.service";
  import {GET_ALL_STUDENT_VACANCIES, GET_LOAD_VACANCIES, VACANCIES} from "../store/types/vacancies";
  import VacancyItem from "../components/UserProfile/VacancyItem";

  export default {
    name: 'company-view',
    components: {VacancyItem},
    props: {
      id: {required: false, type: String},
    },
    data() {
      return {
        company: false
      }
    },
    computed: {
      vacancies() {
        return this.$store.getters[VACANCIES + GET_LOAD_VACANCIES].filter(vacancy => vacancy.companyId === this.id)
      },
    },
    methods: {
      loadCompany(id) {
        if (id) {
          companyService.loadCompany(id)
            .then(company => {
              this.company = company;
            })
            .catch(err => {
              throw err;
            })
        }
      }
    },
    watch: {
      companyId(to){
        this.loadCompany(to);
      }
    },
    mounted() {
      this.$store.dispatch(VACANCIES + GET_ALL_STUDENT_VACANCIES);
      if (this.id) {
        this.loadCompany(this.id);
      }
    }
  }
</script>

