import api, {baseURL} from '../store/api/main'

const studentService = {
  loadProfile(id) {
    return new Promise((resolve, reject) => {
      api.get('student/'+id).then(res => {
        if (res === 'null') {
          throw new Error('Student not found');
        }
        const data = res.data;
        data.student.image = baseURL+'/student/image-avatar/'+id+'.png';
        resolve(data);
      }).catch(err => {
        reject(err);
      })
    });
  },
  loadUserDocs(id) {
    return new Promise((resolve, reject) => {
      api.get('/student/public-documents/'+id)
        .then(res => {
          const files = res.data.files;
          if (!files) {
            throw new Error('No files in response')
          }
          resolve(files);
        }).catch(err => {
          reject(err);
      });
    });
  },
  loadUserSurvey(studentId, setNumber) {
    return new Promise(async (resolve, reject) => {
      const questions = await api.get('questionnaire/question-set/' + setNumber)
        .then(res => {
          return res.data.questionSet[0].questions;
        })
        .catch(err => {
          reject(err);
        });
      const answers = await api.get('/questionnaire/set-answers/' + studentId + '/' + setNumber)
        .then(res => {
          return res.data.answers;
        })
        .catch(err => {
          reject(err);
        });
      resolve(questions.map(question => {
        const answer = answers.find(answer => answer.questionNumber === question.questionNumber);
        if (answer) return {...question, selectedAnswers: answer.answers};
        return question
      }));
    });
  },
};

export default studentService
