<template>
    <div class="chat-message">
        <el-dialog
                :title="'Отправить сообщение ' + receiverTitle"
                :visible.sync="chatVisible"
                :beforeClose="handleClose"
                width="30%">
            <el-input type="textarea"
                      placeholder="Введите сообщение"
                      v-model="messageText"
                      @keyup.enter.native="sendMessage()"
                      :autosize="{ minRows: 3}"></el-input>
            <div class="text-right">
                <div class="mt-4">
                    <el-button type="" @click="handleClose" size="small"><span>Отмена</span></el-button>
                    <el-button type="primary" @click="sendMessage" size="small">Отправить <i class="ml-3 fas fa-check"></i></el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {
        ADD_TO_CHAT,
        ADD_TO_CONVERSATION,
        CHAT,
        CONVERSATIONS,
        GET_SOCKET,
        MESSAGES, USER
    } from "../../store/mutation-types";
    import {AUTH, IS_LOGGED} from "../../store/types/auth";
    import {USER_PROFILE} from "../../store/types/userProfile";
    import {COMPANY_PROFILE, GET_PROFILE} from "../../store/types/companyProfile";
    export default {
        name: 'chat-send-message',
        data() {
            return {
                messageText: '',
                showMessage: false
            }
        },
        props: {
            receiverTitle: {
                type: String,
                required: true
            },
            chatVisible: {
                type: Boolean,
                required: true
            },
            receiverId: {
                type: String,
                required: true
            }
        },
        computed: {
            socket() {
                return this.$store.getters[MESSAGES + GET_SOCKET]
            },
            userRole() {
                return this.$store.getters[AUTH + IS_LOGGED]
            },
            profileStore() {
                return this.userRole === USER ? USER_PROFILE : COMPANY_PROFILE
            },
            profileId() {
                return this.$store.getters[this.profileStore + GET_PROFILE].id
            }
        },
        methods: {
            sendMessage() {
                this.socket.emit('chat_message', {
                    receiverId: this.receiverId,
                    messageType: 'text',
                    text: this.messageText,
                    timeSent: new Date(),
                }, data => {
                    this.$router.push('/profile/chat/' + data.conversationId)
                    const message = {
                        timeSent: new Date(),
                        authorId: this.profileId,
                        messageType: 'text',
                        text: this.messageText
                    }
                    this.$store.dispatch(CONVERSATIONS + ADD_TO_CONVERSATION, {message: message.text, chatId: data.conversationId})
                    this.$store.commit(CHAT + ADD_TO_CHAT, {messages: [message].slice(), chatId: data.conversationId})
                })
            },
            handleClose() {
                this.$emit('update:chatVisible', false)
            }
        }
    }
</script>