<script>
    import {GET_COMPLETED_TASKS, GET_ONGOING_TASKS, GET_STATUS, TASKS} from "../../store/mutation-types";
    import TasksPlaceholder from "../Tasks/TasksPlaceholder";
    export default {
        components: {TasksPlaceholder},
        name: 'company-task-control',
        computed: {
            tasks() {
              return this.$store.getters[TASKS + GET_ONGOING_TASKS].slice().sort((a,b) => b.startDate.getTime() - a.startDate.getTime())
            },
            completedTasks() {
                return this.$store.getters[TASKS + GET_COMPLETED_TASKS].slice().sort((a,b) => b.startDate.getTime() - a.startDate.getTime())
            },
            isLoading() {
                return this.$store.getters[TASKS + GET_STATUS] === 'loading'
            }
        },
        methods: {
            getPercentage(start, end) {
                const startDate = new Date(start).getTime();
                const endDate = new Date(end).getTime();
                const today = (new Date()).getTime();
                return (today > endDate) ? 100 : parseInt((today - startDate)/(endDate - startDate) * 100)
            },
            getStatus(end) {
                const endDate = new Date(end).getTime();
                const today = (new Date()).getTime();
                return (today > endDate) ? 'exception' : ''
            }
        },
        mounted() {
            this.$store.dispatch(TASKS + GET_ONGOING_TASKS)
        }
    }
</script>
