import {
    SET_VACANCY_STATUS, SET_VACANCIES, CREATE_VACANCY, USER, SET_FILTERS, REMOVE_VACANCY,
    GET_REQUEST_VACANCIES
} from '../mutation-types'
import api, {baseURL} from '../api/main'
import {
  GET_ACCEPTED_VACANCIES,
  GET_ALL_STUDENT_VACANCIES,
  GET_ALL_VACANCIES,
  GET_FILTERED_STUDENT_VACANCIES,
  GET_FILTERED_VACANCIES,
  GET_INCOME_VACANCIES,
  GET_LOAD_VACANCIES,
  GET_LOADING_STATUS,
  GET_OUTCOME_VACANCIES,
  GET_OWN_VACANCIES,
  GET_REJECTED_VACANCIES,
  GET_VACANCY,
  MERGE_VACANCIES,
  MERGE_VACANCY,
  SET_LOADED_VACANCY,
  SET_TOTAL_COUNT,
  VACANCY_CLEAN,
  VACANCY_ERROR,
  VACANCY_REQUEST,
  VACANCY_SUCCESS,
} from '../types/vacancies'
import vacancyService from '../../services/vacancies.service'
import {GET_TOTAL_COUNT} from '../types/companies'

const state = {
    all: {},
    totalCount: 0,
    list: [],
    loadedList: [],
    status: 'clear',
    filter: ''
}


const getters = {
    [GET_INCOME_VACANCIES]: (state, getters) => {
        return getters[GET_REQUEST_VACANCIES].filter(v => v.status === 1)
    },
    [GET_OUTCOME_VACANCIES]: (state, getters) => {
        return getters[GET_REQUEST_VACANCIES].filter(v => v.status === 2)
    },
    [GET_ACCEPTED_VACANCIES]: (state, getters) => {
        return getters[GET_REQUEST_VACANCIES].filter(v => v.status === 3)
    },
    [GET_REJECTED_VACANCIES]: (state, getters) => {
        return getters[GET_REQUEST_VACANCIES].filter(v => v.status === 4)
    },
    [GET_REQUEST_VACANCIES]: (state, getters) => {
        return getters[GET_ALL_VACANCIES].filter(v => v.state === 'pending')
    },
    [GET_OWN_VACANCIES]: (state, getters, rootState) => {
        const companyId = rootState.companyProfile.profile.id;
        return getters[GET_ALL_VACANCIES].filter(v => {
            return v.companyId === companyId
        }).sort((a, b) => a.order - b.order)
    },
    [GET_ALL_VACANCIES]: (state, getters, rootState) => {
        const list =  Object.values(state.all).sort((a, b) => a.order - b.order);
        if(rootState.auth.role === USER) {
            list.map(v => {
                return v
            })
        }
        return list
    },
    [GET_VACANCY]: (state) => id => {
        if (!state.all[id]) return null;
        return state.all[id]

    },
    [GET_LOAD_VACANCIES]: (state, getters) => {
        if (state.filter !== '') return getters[GET_FILTERED_VACANCIES];
        return state.loadedList.slice()
    },
    [GET_LOADING_STATUS]: (state) => {
        return state.status
    },
    [GET_FILTERED_VACANCIES]: (state, getters) => {
        return getters[GET_ALL_VACANCIES].filter(v => {
            const searchStr = v.vacancyName + ' '
                + v.vacancyField + ' '
                + v.description + ' '
                + v.companyName + ' '
                + v.demands.join(' ');

            return searchStr.toLowerCase().indexOf(state.filter.toLowerCase()) !== -1
        });
    },
    [GET_TOTAL_COUNT]: (state) => state.totalCount,
}

const mutations = {
    [SET_VACANCY_STATUS] (state, payload) {
        state.find(v => v.id === payload.id).status = payload.val;
        localStorage.setItem('vacancies', JSON.stringify(state))
    },
    [VACANCY_REQUEST] (state) {
        state.status = 'loading'
    },
    [VACANCY_SUCCESS] (state) {
        state.status = 'success'
    },
    [VACANCY_ERROR] (state) {
        state.status = 'error'
    },
    [VACANCY_CLEAN] (state) {
        state.status = 'clean';
        state.all = {};
        state.list = [];
        state.loadedList = []
    },
    [SET_VACANCIES](state, payload) {
        state = payload
    },
    [MERGE_VACANCIES](state, payload) {
        const all = state.all;
        payload.forEach(i => {
            i.id = i._id || i.id;
            delete i._id;
            i.image = baseURL + '/company/image-avatar/' + i.companyId + '.png';
            all[i.id] = i
        });
        state.all = JSON.parse(JSON.stringify(all));
        state.list = Object.values(state.all).sort((a, b) => a.order - b.order)
    },
    [MERGE_VACANCY](state, payload) {
        payload.id = payload._id || payload.id;
        delete payload._id;
        payload.image = baseURL + '/company/image-avatar/' + payload.companyId + '.png';
        payload.order = state.all[payload.id].order || state.all.length;
        state.all[payload.id] = payload;
        state.all = JSON.parse(JSON.stringify(state.all));
        state.list = Object.values(state.all).sort((a, b) => a.order - b.order)
    },
    [SET_LOADED_VACANCY](state, payload) {
        state.loadedList = [];
        payload.forEach(v => {
            state.loadedList.push(state.all[v.id])
        })
    },
    [SET_FILTERS] (state, payload) {
        state.filter = payload
    },
    [REMOVE_VACANCY](state, vacancyId) {
        delete state.all[vacancyId];
        const lI = state.list.findIndex(v => v.id === vacancyId);
        if (lI!==-1)state.list = state.list.splice(lI, 1);
        const llI = state.loadedList.findIndex(v => v.id === vacancyId);
        if (llI!==-1)state.loadedList = state.loadedList.splice(llI, 1)
    },
    [SET_TOTAL_COUNT](state, totalCount) {
        state.totalCount = totalCount;
    }
}
const actions = {
    [CREATE_VACANCY]: (_, payload) => new Promise((resolve, reject) => {
        return api.put('/company/vacancy/', payload)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
            console.log(err);
              window.x = err;
              reject(err)
            })
    }),
    [REMOVE_VACANCY]: (_, payload) => new Promise((resolve, reject) => {
        return api.get('/company/vacancy/remove/'+payload, payload)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                console.log(err);
                reject(err)
            })
    }),
    [GET_ALL_VACANCIES]: ({commit}) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post('vacancy/0/200')
            .then(res => {
                const vacancies = res.data.result.map((i, index) => {
                    i.order = index;
                    i.status = 0;
                    return i
                });
                const totalCount = res.data.totalCount;
                commit(SET_TOTAL_COUNT, totalCount);
                commit(MERGE_VACANCIES, vacancies);
                commit(SET_LOADED_VACANCY, vacancies);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
    [GET_ALL_STUDENT_VACANCIES]: ({commit}, {page=0, count=10} = {}) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post(`/vacancy/${page}/${count}`)
            .then(async res => {
                const vacancies = res.data.result.map((i, index) => {
                    i.order = index;
                    i.status = 0;
                    return i
                });
                for(let i in vacancies) {
                    await vacancyService.loadStudentVacancy(vacancies[i]._id).then(vacancy => {
                      vacancies[i] = vacancy;
                    });
                }
                console.log(vacancies);
                commit(MERGE_VACANCIES, vacancies);
                commit(SET_LOADED_VACANCY, vacancies);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
    [GET_FILTERED_VACANCIES]: ({commit}, {page=0, count=10, filters = {}} = {}) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post(`vacancy/${page}/${count}`, {filter: filters})
            .then(res => {
                const vacancies = res.data.result.map((i, index) => {
                    i.order = index;
                    i.status = 0;
                    return i
                });
                const totalCount = res.data.totalCount;
                commit(SET_TOTAL_COUNT, totalCount);
                commit(MERGE_VACANCIES, vacancies);
                commit(SET_LOADED_VACANCY, vacancies);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                console.log(err.response.status);
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
    [GET_FILTERED_STUDENT_VACANCIES]: ({commit}, {page=0, count=10, filter={}} = {}) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post(`student/vacancy/${page}/${count}`, {filter})
            .then(res => {
                const vacancies = res.data.vacancies.map((i, index) => {
                    i.order = index
                    i.status = i.status || 0;
                    return i
                });
                commit(MERGE_VACANCIES, vacancies);
                commit(SET_LOADED_VACANCY, vacancies);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                console.log(err.response.status);
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
    [GET_OWN_VACANCIES]: ({commit}, {page=0, count=10} = {}) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post(`vacancy/${page}/${count}`)
            .then(res => {
                const vacancies = res.data.result.map((i, index) => {
                    i.order = index;
                    i.status = 0;
                    return i
                });
                const totalCount = res.data.totalCount;
                commit(SET_TOTAL_COUNT, totalCount);
                commit(MERGE_VACANCIES, vacancies);
                commit(SET_LOADED_VACANCY, vacancies);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                console.log(err);
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
    [GET_VACANCY]: ({commit}, id) => new Promise((resolve, reject) => {
        commit(VACANCY_REQUEST);
        return api.post('vacancy/' + id)
            .then(res => {
                const vacancy = res.data;
                vacancy.status = vacancy.status || 0;
                commit(MERGE_VACANCY, vacancy);
                commit(VACANCY_SUCCESS);
                resolve(res)
            })
            .catch(err => {
                console.log(err);
                commit(VACANCY_ERROR);
                reject(err)
            })
    }),
}
export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}
