<template>
  <div class="student-view px-0 py-5 px-md-5" v-loading="status === 'LOADING'">
    <template v-if="user">
      <div class="d-flex profile-info mb-5">
        <div class="mr-3 mr-md-5">
          <div class="image-container">
            <div class="profile-image" :style="{backgroundImage: `url(${user.image})`}"></div>
          </div>
        </div>
        <div class="mr-sm-5">
          <div class="d-flex align-items-center mb-3">
            <h3 class="mr-4" v-if="user.firstName || user.lastName">{{ (user.firstName || '') + ' ' + (user.lastName || '')}}</h3>
            <h3 class="mr-4" v-if="!user.firstName && !user.lastName">Имя не указано</h3>
          </div>
          <div class="mb-3 icon-text">
            <template v-if="user.phone">
              <a :href="`tel: ${user.phone}`">
                <i class="fa fa-mobile-alt mr-2"></i> {{user.phone}}
              </a>
            </template>
            <template v-if="!user.phone">
              <i class="fa fa-mobile-alt mr-2"></i> Телефон не указан
            </template>
          </div>
          <div class="icon-text">
            <a :href="`mailto: ${user.credentials.email}`" v-if="user.credentials.email">
              <i class="far fa-envelope mr-2"></i> {{user.credentials.email}}
            </a>
          </div>
          <div class="icon-text" v-if="!user.credentials.email">
            <i class="far fa-envelope"></i> Почта не указана
          </div>
        </div>
      </div>
      <div class="profile-actions">
        <el-button @click="vacancyDialogVisible = true" size="small" icon="el-icon-circle-plus-outline" type="primary" class="mb-3 mr-3" style="width: 180px">Предложить вакансию</el-button>
        <el-button @click="messageDialogVisible = true" size="small" icon="el-icon-message" type="warning" class="mb-3 ml-0" style="width: 180px">Написать сообщение</el-button>
      </div>
      <div class="py-5" v-if="user.description">
        <h3>Информация</h3>
        <p v-html="user.description"></p>
      </div>
      <div class="py-5" v-if="documents && documents.length > 0">
        <h3 class="mb-4">Документы</h3>
        <div class="row">
          <div class="col-md-6 mb-3" v-for="(document, i) in documents" :key="i">
            <document-item :document="document"></document-item>
          </div>
        </div>
      </div>
      <div class="py-5 my-5" v-if="tests && tests !== {}">
        <h3 class="my-4">Тесты</h3>
        <div class="row">
          <div class="col-lg-4 col-md-6" v-if="tests.belbin && tests.belbin.length">
            <belbin-results :results="tests.belbin"/>
          </div>
          <div class="col-lg-4 col-md-6" v-if="tests.survey">
            <survey-results :results="tests.survey"/>
          </div>
        </div>
      </div>
      <el-dialog
        title="Предложить вакансию"
        :visible.sync="vacancyDialogVisible"
        width="30%">
        <SendVacancy :personId="id" @onSubmit="vacancyDialogVisible = false" @onError="vacancyDialogVisible = false"/>
      </el-dialog>
      <chat-send-message :chat-visible.sync="messageDialogVisible"
                         :receiver-id="id"
                         :receiver-title="(user.firstName ? user.firstName : user.credentials.email)"/>
    </template>
  </div>
</template>

<script>
  import DocumentItem from '../components/UserProfile/UserDocs/DocumentItem'
  import CoffeeCup from '../components/UserProfile/CoffeeCup'
  import studentService from '../services/student.service'
  import {CLEAN, ERROR, LOADING, SUCCESS} from '../store/mutation-types'
  import SendVacancy from './SendVacancy'
  import ChatSendMessage from '../components/Chat/ChatSendMessage'
  import BelbinResults from "../components/Surveys/BelbinResults";
  import SurveyResults from "../components/Surveys/SurveyResults";

  export default {
    name: 'student-view',
    components: {SurveyResults, BelbinResults, ChatSendMessage, SendVacancy, DocumentItem, CoffeeCup},
    props: {
      id: {required: true, type: String}
    },
    data() {
      return {
        status: CLEAN,
        user: false,
        vacancyDialogVisible: false,
        messageDialogVisible: false,
        loading: false,
        documents: false,
        tests: {},
      }
    },
    methods: {
      loadProfile() {
        this.status = LOADING;
        studentService.loadProfile(this.id).then(({student}) => {
          this.user = student;
          this.tests = {...this.tests, belbin: student.belbinResults};
          this.status = SUCCESS
        }).catch(err => {
          this.status = ERROR;
          throw err;
        });
      },
      loadSurveys() {
        studentService.loadUserSurvey(this.id, 1)
          .then(data => {
            this.tests = {...this.tests, survey: data};
          })
          .catch(err => {
            console.log(err);
          })
      },
      loadDocs() {
        studentService.loadUserDocs(this.id)
          .then(files => {
            this.documents = files;
          });
      },
    },
    mounted() {
      this.loadProfile();
      this.loadDocs();
      this.loadSurveys();
    },
  }
</script>

