import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '../views/NotFound.vue'
import Home from '../views/Home.vue'
import Chat from '../views/Chat.vue'
import Dashboard from '../views/Dashboard.vue'
import Documents from '../views/Documents.vue'
import Profile from '../views/Profile.vue'
import ProfileUnconfirmed from '../views/ProfileUnconfirmed.vue'
import Vacancies from '../views/Vacancies.vue'
import Survey from '../views/Survey.vue'
import Request from '../views/Request.vue'
import Workers from '../views/Workers.vue'
import Settings from '../views/Settings.vue'
import TaskControl from '../views/TaskControl.vue'
import TaskContainer from '../views/TaskContainer.vue'
import StudentView from '../views/StudentView.vue'
import CompanyView from '../views/CompanyView.vue'
import LoginModal from '../components/ClientModals/LoginModal.vue'
import RegisterModal from '../components/ClientModals/RegisterModal.vue'
import LoginCompanyModal from '../components/CompanyModals/LoginModal.vue'
import RegisterCompanyModal from '../components/CompanyModals/RegisterModal.vue'
import {accountConfirmed, accountUnconfirmed, requireAuth, unrequireAuth} from "./router-guards";
import UserSearch from "../components/UserProfile/UserSearch";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/profile',
      component: Dashboard,
      beforeEnter: requireAuth,
      children: [
        {
          path: '/',
          name: 'profile',
          component: Profile,
        },
        {
          path: '/student:id',
          name: 'student-page',
          component: StudentView,
          props: (route) => ({id: route.params.id}),
        },
        {
          path: '/company:id',
          name: 'company-page',
          component: CompanyView,
          props: (route) => ({id: route.params.id}),
        },
        {
          path: 'survey/:set',
          name: 'survey',
          component: Survey,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'request',
          name: 'request',
          component: Request,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'workers',
          name: 'workers',
          component: Workers,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'companies',
          name: 'companies',
          component: UserSearch,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'settings',
          name: 'settings',
          component: Settings,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'chat',
          name: 'chat',
          component: Chat,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'chat/:id',
          name: 'chat-conversation',
          component: Chat,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'docs',
          name: 'documents',
          component: Documents,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'task-control',
          name: 'task-control',
          component: TaskControl,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'task-control/:id',
          name: 'ongoing-task',
          component: TaskContainer,
          beforeEnter: accountConfirmed,
        },
        {
          path: 'unconfirmed',
          name: 'unconfirmed',
          component: ProfileUnconfirmed,
          beforeEnter: accountUnconfirmed,
        },
        {
          path: 'not-found',
          name: 'profile-not-found',
          component: NotFound
        },
        {
          path: '*',
          redirect: '/profile/not-found'
        }
      ]
    },
    {
      path: '/vacancies',
      name: 'vacancies',
      component: Vacancies
    },
    {
      path: '/',
      name: 'home',
      component: Home,
      beforeEnter: unrequireAuth,
      meta: {showModal: false},
      children: [
        {
          path: 'register',
          name: 'register',
          component: RegisterModal,
          meta: {showModal: true},
        },
        {
          path: 'login',
          name: 'login',
          component: LoginModal,
          meta: {showModal: true},
        },
        {
          path: 'register-company',
          name: 'register-company',
          component: RegisterCompanyModal,
          meta: {showModal: true},
        },
        {
          path: 'login-company',
          name: 'login-company',
          component: LoginCompanyModal,
          meta: {showModal: true},
        },
      ]
    },
    {
        path: '/**',
        name: 'not-found',
        component: NotFound
    }
  ],
  scrollBehavior(to) {
    if (to.hash) {
      return {selector: to.hash, offset: {x:0, y: 150}}
    }
    return { x: 0, y: 0 };
  }
})
