<template>
    <div class="chat-wrapper row mx-0">
        <div class="col-lg-4 chat-list-container px-0" :class="{scrolled: scrolled}">
            <chat-list :selectedChat="$route.params.id" :chats="chats" :scrolled.sync="scrolled" :chatRequest="chatRequest"></chat-list>
        </div>
        <div class="col-lg-8 chat-conversation-container px-0">
            <chat-conversation :chat="selectedChat"></chat-conversation>
        </div>
    </div>
</template>

<script>
    import ChatConversation from "./ChatConversation";
    import ChatList from "./ChatList";
    import {AUTH, GET_TYPE} from "../../store/types/auth";
    import {CONVERSATIONS, GET_CONVERSATIONS, GET_STATUS} from "../../store/mutation-types";
    import {baseURL} from "../../store/api/main";
    export default {
      components: {
        ChatList,
        ChatConversation},
      name: 'chat-container',
      data(){
        return {
          scrolled: true
        }
      },
        computed: {
            userRole() { return this.$store.getters[AUTH + GET_TYPE] },
            receiverRole() { return this.userRole==='student' ? 'company' : 'student' },
            chats() {
                const conversations = this.$store.getters[CONVERSATIONS + GET_CONVERSATIONS]
                const chats = []
                conversations.forEach(c => {
                    chats.push({
                        chatImage: this.getChatAvatar(c),
                        chatId: c._id,
                        chatMessage: c.lastMessage.text,
                        chatName: this.getSenderName(c),
                        chatReceiver: this.getSenderId(c),
                        lastMessageId: c.lastMessage._id,
                        unread: false,
                    })
                });
                return chats;
            },
            selectedChat() {
                return this.chats.find(c => c.chatId === this.$route.params.id)
            },
            chatRequest() {
                return this.$store.getters[CONVERSATIONS + GET_STATUS]
            }
        },
        methods: {
            getSenderName(c) {
                return (this.userRole === 'student') ?
                    (c.company.name ? c.company.name : c.company.id) :
                    ((c.student.firstName || c.student.lastName) ? `${c.student.firstName} ${c.student.lastName}` : c.student.id)
            },
            getSenderId(c) {
                return (this.userRole === 'student') ? c.company.id : c.student.id
            },
            getImageUrl(id) {
                return `${baseURL}/${this.receiverRole}/image-avatar/${id}.png`
            },
            getChatAvatar(c) {
                return this.getImageUrl(this.getSenderId(c))
            }
        },
        mounted() {
            this.$store.dispatch(CONVERSATIONS + GET_CONVERSATIONS, this.userRole)
        }
    }
</script>

<style lang="scss">
    .el-main {
        overflow: visible;
    }
    .chat-wrapper {
        border-radius: 10px;
        overflow: hidden;
        box-shadow: 0 5px 50px rgba(0,0,0,.075);
        position: relative;
    }
    @media (max-width: 992px) {
        .chat-wrapper {
            padding-left: 60px;
        }
        .chat-list-container {
            height: 100%;
            position: absolute !important;
            left: 0;
            overflow: hidden;
            max-width: none;
            z-index: 2;
            .rotating-switch {
                transform: rotate(-180deg);
                transition: .2s;
            }
            &.scrolled {
                .chat-list-header {
                    padding: 0 15px !important;
                    .rotating-switch {
                        margin-right: 5px;
                    }
                }
                max-width: 60px;
                z-index: 1;
                .rotating-switch {
                    transform: rotate(0deg);
                }
                .chat-list-ul li {
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }
        }
        .chat-conversation-container {
            border-left: 1px solid #ebedf3;
        }

    }
</style>