<template>
    <div class="user-actions">
        <el-button-group class="w-100" size="small" v-if="userRole === USER">
            <el-tooltip :enterable="false" :disabled="modalVisible" class="item" effect="dark" content="Подробнее" placement="left">
                <el-button class="w-50" icon="far fa-file-alt" size="mini" @click="dialogVisible = true"></el-button>
            </el-tooltip>
            <el-tooltip :enterable="false" :disabled="modalVisible" class="item" effect="dark" content="Написать" placement="right">
                <el-button class="w-50" icon="far fa-envelope" size="mini" @click="chatVisible = true" plain></el-button>
            </el-tooltip>
        </el-button-group>
        <template v-if="userRole === COMPANY">
            <el-button class="ml-auto d-block" @click="dialogVisible = true" round icon="far fa-file-alt"><span class="ml-3 d-none d-sm-inline">Подробнее</span></el-button>
        </template>
        <el-dialog :title="vacancy.vacancyName"
                :visible.sync="dialogVisible"
                width="50%"
        >
            <p class="mb-4">Компания: {{vacancy.companyName}}</p>
            <p v-if="vacancy.description" v-html="vacancy.description"></p>
            <template v-if="vacancy.demands && vacancy.demands.length > 0">
                <div>
                    <p class="d-inline-block">Тип занятостив:</p>
                    <ul class="mb-3 tags-list">
                        <li v-for="(requirement, index) in vacancy.demands" :key="index">
                            {{requirement}}
                        </li>
                    </ul>
                </div>
            </template>
            <div style="opacity: .5">
                <p class="mb-0" v-if="vacancy.cost">Заработная плата: {{vacancy.cost}}</p>
                <!--<p class="mb-0" v-if="vacancy.deadline">Срок сдачи: {{vacancy.deadline | moment('Do MMMM, YYYY')}}</p>-->
            </div>
        </el-dialog>
        <chat-send-message :receiverTitle="vacancy.companyName"
                           :chatVisible.sync="chatVisible"
                           :receiverId="vacancy.companyId">
        </chat-send-message>
    </div>
</template>

<script>
    import {
        COMPANY,
        USER
    } from "../../store/mutation-types";
    import {AUTH, IS_LOGGED} from "../../store/types/auth";
    import ChatSendMessage from "../Chat/ChatSendMessage";
    export default {
        components: {ChatSendMessage},
        name: 'company-actions',
        data() {
            return {
                dialogVisible: false,
                chatVisible: false,
                messageText: '',
                USER,
                COMPANY,
            }
        },
        props: {
            vacancy: {
                type: Object,
                required: true
            }
        },
        computed: {
          modalVisible() {
            return this.dialogVisible || this.chatVisible
          }, 
          userRole() {
            return this.$store.getters[AUTH + IS_LOGGED]
          }
        }
    }
</script>