<template>
    <el-radio-group v-model="selected"
                    @change="handleChange($event)"
                    v-if="survey"
                    v-loading="loading"
    >
        <el-radio v-for="(answer, j) in survey.answers"
                  :key="j"
                  :label="answer"
        >{{answer}}</el-radio>
    </el-radio-group>
</template>

<script>
    import {SET_SURVEY_ANSWER, SURVEYS} from "../../../store/types/surveys";
    export default {
        name: 'single-choice',
        data() {
            return {
                selected: '',
                loading: false
            }
        },
        props: {
            survey: {},
            setNumber: Number
        },
        methods: {
            handleChange(value) {
                this.loading = true
                const setNumber = this.setNumber
                const questionNumber = this.survey.questionNumber
                this.$store.dispatch(SURVEYS + SET_SURVEY_ANSWER, {setNumber, questionNumber, answers: [value]})
                    .then(() => {
                        this.loading = false
                    })
                    .catch(() => {
                        this.loading = false
                    })
            },
          handleSelectedAnswer(survey) {
            if (survey.selectedAnswer && survey.selectedAnswer[0]) {
              this.selected = survey.selectedAnswer[0]
            }
          }
        },
      watch: {
        survey(to) {
          this.handleSelectedAnswer(to)
        }
      },
      mounted() {
        if (this.survey) {
          this.handleSelectedAnswer(this.survey)
        }
      }
    }
</script>