<template>
    <div class="px-0 py-5 px-md-5">
        <h2 class="mb-5">Поиск специалистов</h2>
        <filter-people></filter-people>
        <div class="row my-5 py-5" v-loading="isLoading">
            <student-item class="col-12 mb-5" v-for="(person, index) in people" :key="index + '-person'" :personId="person.id" action="contacts"></student-item>
        </div>
        <el-pagination
          v-if="totalCount > pageSize"
          class="text-center"
          background
          layout="prev, pager, next"
          @current-change="pageChange"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="totalCount"
        >
        </el-pagination>
    </div>
</template>

<script>
    import FilterPeople from './FilterPeople.vue'
    import {GET_ALL_STUDENTS, STUDENTS} from "../../store/types/students";
    import StudentItem from "./StudentItem";
    import {COMPANY_PROFILE, GET_PROFILE} from "../../store/types/companyProfile";
    import {GET_STUDENTS_FLEX} from "../../store/mutation-types";
    import {GET_TOTAL_COUNT} from '../../store/types/companies'

    export default {
        name: 'company-search',
        components: {
            StudentItem,
            FilterPeople,
        },
        data() {
            return {
              currentPage: 1,
              pageSize: 10,
            }
        },
        computed: {
            people(){
                return this.$store.getters[STUDENTS + GET_STUDENTS_FLEX]
            },
            isLoading(){
                return this.$store.state.students.status === 'loading'
            },
            totalCount() {
              return this.$store.getters[STUDENTS + GET_TOTAL_COUNT]
            }
        },
      methods: {
        pageChange(page) {
          this.loadStudents(page-1, this.pageSize);
        },
        loadStudents(page = 0, count=10) {
          this.$store.dispatch(STUDENTS + GET_ALL_STUDENTS, {page, count});
        },
      },
        mounted() {
            if (this.$store.state.companyProfile.status === 'clean') {
              this.$store.dispatch(COMPANY_PROFILE + GET_PROFILE)
            }
            this.loadStudents(this.currentPage - 1, this.pageSize);
        }
    }
</script>
