<template>
    <div class="chat-list">
        <div class="chat-list-header d-flex align-items-center">
            <i class="fas fa-address-book d-none d-lg-inline-block chat-icon"></i>
            <span class="rotating-switch chat-icon d-lg-none d-inline-block" @click="scrolled ? emitOpen() : emitClose()">
                <i class="fas fa-angle-double-right"></i>
            </span>
            <h3>Диалоги</h3>
        </div>
        <template v-if="chatRequest==='success'">
            <ul class="chat-list-ul" v-if="chats && chats.length > 0">
                <router-link tag="li"
                             class="d-flex align-items-center"
                             v-for="(chat, index) in chats"
                             :key="index"
                             :to="'/profile/chat/' + chat.chatId"
                             :class="{'active': selectedChat===chat.chatId}">
                    <div class="chat-avatar" :style="{backgroundImage: `url(${chat.chatImage})`}"></div>
                    <div class="w-100">
                        <span class="chat-name">{{chat.chatName}}</span>
                        <span class="chat-message">{{chat.chatMessage}}</span>
                        <span class="chat-unread" v-if="selectedChat !== chat.chatId && chat.unreadCount && chat.unreadCount > 0">{{chat.unreadCount}}</span>
                    </div>
                </router-link>
            </ul>
            <div class="chat-list-ul" v-if="chats && chats.length == 0">
                <h4 style="color: #aaaaaa" class="text-center py-5">У вас нет диалогов</h4>
            </div>
        </template>
        <template v-if="chatRequest==='loading'">
            <div class="chat-list-ul">
                <h4 style="color: #aaaaaa" class="text-center py-5">Загружаем...</h4>
            </div>
        </template>
    </div>
</template>

<script>
  export default {
    name: 'chat-list',
    props: {
      selectedChat: String,
      chats: {
        chatImage: String,
        chatId: String,
        chatMessage: String,
        chatName: String,
        unread: Boolean,
      },
      chatRequest: String,
      scrolled: Boolean,
    },
    watch: {
      selectedChat() {
        this.emitClose();
      }
    },
    methods: {
      emitClose() {
        this.$emit('close')
        this.$emit('update:scrolled', true)
      },
      emitOpen() {
        this.$emit('open')
        this.$emit('update:scrolled', false)
      }
    }
  }
</script>

