        li {
            padding: 15px 20px;
            position: relative;
            color: #4e4e4e;
            border-bottom: 1px solid #f5f5f5;
            transition: .1s;
            cursor: pointer;
            &:hover {
                background-color: #ededed;
                border-bottom: 1px solid #EDEDED;
                transition: 0s;
            }
            &.active {
                background-color: #4a89ff;
