<template>
    <div class="survey-results">
        <icon-panel icon="far fa-question-circle" text="Результаты опросника" color="#27862a" @click="visible = true"/>
        <el-dialog :visible.sync="visible" title="Результаты опросника">
            <div class="row">
                <div class="col-lg-6 mb-5" v-for="result in list" :key="'result_' + result.questionNumber">
                    <h4 class="d-flex"><i class="fas fa-bullseye mr-3" style="margin-top: 2px; color: #78c778"></i> <span class="d-inline-block">{{result.questionText}}</span></h4>
                    <p class="ml-5" style="opacity: .7">
                        {{result.selectedAnswers.join(', ')}}
                    </p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
  import IconPanel from "../_shorts/IconPanel";
  export default {
    name: 'survey-results',
    components: {IconPanel},
    props: {
      results: {required: true, type: Array},
    },
    data() {
      return {
        visible: false,
        console,
      }
    },
    computed: {
      list() {
        return this.results.map(r => {
          if (!r.selectedAnswers || !r.selectedAnswers.length) return {...r , selectedAnswers: ['Не ответил']};
          return r;
        }).sort((a, b) => {
          if (a.selectedAnswer === ['Не ответил']) return 2;
          return a.questionNumber - b.questionNumber;
        });
      },
    },
    methods: {
      openDialog() {
        alert('hil');
      }
    },
  }
</script>
